import React, { useState } from 'react';
import '../../styles/LogBar.scss';


const LoginForm = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleForm = async (event) => {
    event.preventDefault();

    if (!username || !password) {
      setError('Por favor, complete todos los campos.');
      return;
    }

    setLoading(true);

    try {
      const response = await fetch(`${process.env.URL_BACKEND}/auth/login`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, password }),
      });

      const data = await response.json();

      console.log(data);

      setUsername('');
      setPassword('');
      setError('');
    } catch (err) {
      // Si ocurre un error (por ejemplo, credenciales incorrectas)
      console.error('Error al enviar los datos:', err);
      setError('Hubo un error al iniciar sesión. Intenta nuevamente.');
    } finally {
      setLoading(false); // Termina el estado de carga
    }
  };

  return (
    <header className="header-container">
      <div className="login-wrapper">
        <h2 className="login-title">Iniciar sesión</h2>
        <form className="login-form" onSubmit={handleForm}>
          <input
            type="text"
            placeholder="Usuario"
            className="login-input"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
          <input
            type="password"
            placeholder="Contraseña"
            className="login-input"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          {error && <p className="error-message">{error}</p>}
          <button className="login-button" type="submit" disabled={loading}>
            {loading ? 'Cargando...' : 'Login'}
          </button>
        </form>
      </div>
    </header>
  );
};

export default LoginForm;
